.automation-card {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    z-index: 9999;
    width: 30%;
  
  }
  
  
  
  
  .label{
    font-weight: bold;
  }
  
  
  .accordions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f3f6f9;
    padding: 2vh;
    width: 80%;
    color: #adb0bb;
  }
  
  .accordions_dropdown {
    margin-top: 5vh;
  }
  
  .input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }
  
  .NameInput {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
    background-color: #f3f6f9;
    color: #333333;
  }
  
  select {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 400;
  }
  
  .span {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 300;
  }
  
  .input-group label {
    margin-bottom: 8px;
    color: #4e545b;
    font-weight: 500;
  }
  
  .input-group input,
  .input-group,select {
    padding: 10px;
    border: 0px solid #ccc;
    font-size: 14px;
    outline: none;
    transition: border-color 0.3s ease;
  }
  
  .input-group select {
    border: 1px solid #dadee3;
    border-radius: 5px;
    background-color: #ffffff;
    color: #4b4f52;
  }
  
  .input-group input:focus,
  .input-group select:focus {
    border-color: #007bff;
  }
  
  .label-group {
    margin-bottom: 20px;
  }
  
  .last-triggered-label {
    color: #666;
  }
  
  /* .start-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 20px;
    padding: 1vh;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  } */
  
  select {
    background-color: #333333;
    color: white;
    border-radius: 20px;
  }
  
  .start-button:hover {
    background-color: #0056b3;
  }
  
  .label-group label {
    color: #4e545b;
    font-weight: bold;
    font-size: 16px;
  }
  
  .delete-button {
    background-color: #ff6347; /* Tomato color */
    color: #fff; /* White text color */
    border: none;
    border-radius: 5px;
    /* Makes the button round */
    padding: 2vh; /* Adjust padding as needed */
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin: 2vh;
  }
  
  .delete-button:hover {
    background-color: #d6341c; /* Darker red color on hover */
  }
  .delete-icon {
    margin-right: 5px;
  }
  
  .start-button {
    background-color: #13c56b; /* Green */
    border: none;
    color: white;
    padding: 2vh;
    text-align: center;
    text-decoration: none;
    display: flex;
    font-weight: lighter;
    align-items: center;
    border-radius: 5px;
    height: 5vh;
    cursor: pointer;
    transition-duration: 0.4s;
  }
  
  .start-button:hover {
    background-color: #45a049; /* Darker green */
  }
  
  .start-button svg{
    height: 3vh;
    width: 3vh;
  }
  
  .start-icon {
    margin-right: 0.3vh;
    display: flex;
    align-items: center;
     /* Adjust the spacing between the icon and the text */
  }
  
  .buttons {
    display: flex;
    align-items: center;
    padding: 10px;
  }
  
  .card-head{
    display: flex;
    flex-direction: row;
    align-items: flex-start;  
    justify-content: space-between;
    width: 95%;
  }
  
  .name-div{
    margin-top: 1vh;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .close-card :hover{
    background-color: #ed5e5e;
  }
  
  .close-card{
    color: #333333;
  }
  
  
  
  /* Responsive styles */
  @media only screen and (max-width: 600px) {
  }
  
  /* Media query for responsiveness */
  @media screen and (max-width: 768px) {
  }
  
  @media screen and (max-width: 480px) {
    input-group label {
      font-size: 8px;
    }
  }
  